@import 'ScssHelpers/helpers-all.scss';

.swiper-scrollbar {
	bottom: 0 !important;
	left: 0 !important;

	width: 100% !important;
	height: 1px !important;
	background: $black_2 !important;

	@include bp($point_2, min) {
		.venue_spaces &,
		.our_team & {
			display: none;
		}
	}
}

.swiper-scrollbar-drag {
	top: -3px;

	height: 3px !important;

	border-radius: unset !important;
	background: $black_2;

	cursor: grab;
}

.swiper-scrollbar--small_mod {
	max-width: 120rem;
	left: 50% !important;
	transform: translateX(-50%) !important;
	margin: 0 auto;
}
