@import 'ScssHelpers/helpers-all.scss';

.banner_promo {
	padding: 0;
}

.banner_promo__body {
	@include bp($point_2, min) {
		display: flex;
	}
}

.banner_promo__bg {
	position: relative;

	display: flex;
	align-items: flex-end;

	@include bp($point_2, min) {
		width: 50%;
	}

	&:not(.banner_promo--coral_mod &, .banner_promo--green_mod &) {
		@include bp($point_3) {
			order: 2;
		}
	}

	.banner_promo--coral_mod & {
		@include bp($point_2, min) {
			background-color: $coral;
		}

		@include bp($point_3) {
			background-color: $coral_2;
		}
	}

	.banner_promo--green_mod & {
		background-color: $green;
	}

	.banner_promo--reversed_mod & {
		@include bp($point_2, min) {
			order: 2;
		}
	}
}

.banner_promo__image {
	width: 100%;

	@include bp($point_2, min) {
		padding-top: 15.4%;
	}

	@include bp($point_3) {
		padding-top: 5.2rem;
	}
}

.banner_promo__info {

	@include bp($point_2, min) {
		align-self: center;
		width: 50%;

		&:not(.banner_promo--reversed_mod &) {
			padding: 2rem 2rem 2rem 8.2%;
		}

		.banner_promo--reversed_mod & {
			padding: 2rem 8.2% 2rem 2rem;
		}
	}

	@include bp($point_3) {
		width: 100%;

		padding: 5.4rem 4rem 10rem 4rem;
	}

	&:not(.banner_promo--coral_mod &, .banner_promo--green_mod &) {
		@include bp($point_3) {
			padding: 6rem 4rem 5rem 4rem;
		}
	}

	.banner_promo--coral_mod & {
		@include bp($point_3) {
			background-color: $coral;
		}
	}

	.banner_promo--reversed_mod & {
		@include bp($point_2, min) {
			order: 1;
		}
	}
}

.banner_promo__slider_item {
	padding: 0 2rem;

	@include bp($point_3) {
		.banner_promo--mask-1_mod & {
			padding: 0;
		}
	}
}

.banner_promo__picture {
	display: block;

	position: relative;


	mask-position: 50% 0;
	-webkit-mask-position: 50% 0;
	mask-repeat: no-repeat;
	-webkit-mask-repeat: no-repeat;

	.banner_promo--mask-1_mod & {
		mask-image: url('../../images/masks/mask_1.svg');
		-webkit-mask-image: url('../../images/masks/mask_1.svg');
	}

	.banner_promo--mask-2_mod & {
		mask-image: url('../../images/masks/mask_2.svg');
		-webkit-mask-image: url('../../images/masks/mask_2.svg');
	}

	.banner_promo--mask-3_mod & {
		mask-image: url('../../images/masks/mask_3.svg');
		-webkit-mask-image: url('../../images/masks/mask_3.svg');
	}

	.banner_promo--mask-4_mod & {
		mask-image: url('../../images/masks/mask_4.svg');
		-webkit-mask-image: url('../../images/masks/mask_4.svg');
	}

	@include bp($point_2, min) {
		padding-bottom: 103%;

		.banner_promo--mask-1_mod & {
			mask-size: 89%, 100%;
			-webkit-mask-size: 89%, 100%;
		}

		.banner_promo--mask-2_mod & {
			mask-size: 84%, 100%;
			-webkit-mask-size: 84%, 100%;
		}

		.banner_promo--mask-3_mod & {
			mask-size: 84%, 100%;
			-webkit-mask-size: 84%, 100%;
		}

		.banner_promo--mask-4_mod & {
			mask-size: 84%, 100%;
			-webkit-mask-size: 84%, 100%;
		}
	}

	@include bp($point_3) {
		padding-bottom: 122.4%;

		.banner_promo--mask-1_mod & {
			mask-size: 100%, 100%;
			-webkit-mask-size: 100%, 100%;
		}

		.banner_promo--mask-2_mod & {
			mask-size: 100%, 100%;
			-webkit-mask-size: 100%, 100%;
		}

		.banner_promo--mask-3_mod & {
			mask-size: 100%, 100%;
			-webkit-mask-size: 100%, 100%;
		}

		.banner_promo--mask-4_mod & {
			mask-size: 100%, 100%;
			-webkit-mask-size: 100%, 100%;
		}
	}
}

.banner_promo__img {
	position: absolute;
	top: 0;
	left: 0;

	display: block;

	width: 100%;
	height: 100%;

	object-fit: cover;
}

.banner_promo__content {
	width: 100%;

	@include bp($point_2, min) {
		max-width: 50rem;
	}

	&:not(.banner_promo--reversed_mod &) {
		margin-right: auto;
	}

	.banner_promo--reversed_mod & {
		margin-left: auto;
	}

	h4 {
		font-weight: 700;
		letter-spacing: 0.2em;
		text-transform: uppercase;

		font-size: 1.6rem;
		line-height: 262%;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 1.2rem;
			}

			@include bp($point_3) {
				margin-bottom: 1rem;
			}
		}
	}

	h3 {
		max-width: 50rem;

		font-weight: 700;
		font-family: $font_2;

		@include bp($point_2, min) {
			font-size: 5.2rem;
			line-height: 1.25;
		}

		@include bp($point_3) {
			font-size: 3.2rem;
			line-height: 1.1;
		}

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 4rem;
			}

			@include bp($point_3) {
				margin-bottom: 2rem;
			}
		}
	}

	strong {
		display: block;

		font-family: $font_2;
		font-weight: 400;
		font-style: italic;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 2.5rem;
			}

			@include bp($point_3) {
				margin-bottom: 0.8rem;
			}
		}

		@include bp($point_2, min) {
			font-size: 3.2rem;
			line-height: 1.2;
		}

		@include bp($point_3) {
			font-size: 1.8rem;
			line-height: 1.38;
		}
	}

	p {
		@include text;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 2rem;
			}

			@include bp($point_3) {
				margin-bottom: 1.6rem;
			}
		}

		&:nth-last-child(-n+2) {
			@include bp($point_2, min) {
				margin-bottom: 4rem;
			}

			@include bp($point_3) {
				margin-bottom: 3.2rem;
			}
		}

		a {
			display: inline;

			max-width: 100%;

			padding: 0;

			text-decoration: underline;
			color: $black_2;
			font-weight: 400;
			border: none;

			&:hover {
				text-decoration: none;
			}
		}
	}

	a {
		&:not(.banner_promo__content p a) {
			@include btn_size_normal;

			@include bp($point_2, min) {
				max-width: 15.4rem;

				@include btn_style_white;
			}

			@include bp($point_3) {
				max-width: 14.5rem;

				@include btn_style_black;
			}
		}
	}
}

.banner_promo .swiper-pagination {
	margin-bottom: 2.4rem;

	@include bp($point_3) {
		display: none;
	}
}

.banner_promo .swiper-pagination .swiper-pagination-bullet {
	width: 1.5rem;
	height: 1.5rem;

	margin: 0 1rem;

	background: transparent;

	border: 1px solid $white_2;
	opacity: 1;

	transition: background 0.3s ease, opacity 0.3s ease;

	&.swiper-pagination-bullet-active {
		background: $white_2;
	}
}

