@mixin h1 {
	font-weight: 700;
	line-height: 100%;
	font-family: $font_2;

	@include bp($point_2, min) {
		font-size: 8.8rem;
	}
	@include bp($point_3) {
		font-size: 4.4rem;
	}
}

@mixin h2 {
	font-weight: 700;
	line-height: 110%;

	@include bp($point_2, min) {
		font-size: 5.6rem;
	}
	@include bp($point_3) {
		font-size: 3.2rem;
	}
}

@mixin h3 {
	font-size: 3.2rem;
	font-weight: 700;
	line-height: 110%;
}

@mixin h4 {
	line-height: 120%;
	@include bp($point_2, min) {
		font-size: 2.8rem;
	}
	@include bp($point_3) {
		font-size: 2rem;
	}
}

@mixin h5 {
	@include bp($point_2, min) {
		font-size: 2.4rem;
	}
	@include bp($point_3) {
		font-size: 2rem;
	}
}

@mixin h6 {
	font-size: 2rem;
}

@mixin text {
	@include bp($point_2, min) {
		font-size: 1.8rem;
		line-height: 130%;
		letter-spacing: 0.03em;
	}
	@include bp($point_3) {
		font-size: 1.6rem;
		line-height: 140%;
		letter-spacing: 0.02em;
	}
}

@mixin subtitle {
	font-weight: 700;
	letter-spacing: 0.2em;
	text-transform: uppercase;

	@include bp($point_2, min) {
		font-size: 1.6rem;
		line-height: 262%;
	}
	@include bp($point_3) {
		font-size: 1.4rem;
		line-height: 140%;
	}
}
// offsets

@mixin title_offset {
	margin-bottom: 2rem;
}
