@mixin st_btn {
	display: inline-flex;
	justify-content: center;
	align-items: center;

	text-align: center;
	white-space: nowrap;
	font-family: $font_1;

	cursor: pointer;

	&[type='submit'] {
		@include appearance(none);
	}

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

//color mixins:
@mixin btn_style_black {
	font-weight: 700;
	color: $white_2;
	background-color: $black_2;
	transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease;
	border: 1px solid $white_2;
	@include st_btn;

	&:hover {
		background-color: rgba($black_2, 0);
		color: $black_2;
		border-color: $black_2;
	}
}

@mixin btn_style_white {
	font-weight: 700;
	color: $black_2;
	letter-spacing: 0.03em;
	background-color: transparent;
	transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease;
	border: 2px solid $black_2;

	.news_banner--bg_black_mod & {
		border-color: $white_2;
		color: $white_2;
	}

	@include st_btn;

	&:hover {
		background-color: $black_2;
		color: $white_2;

		.news_banner--bg_black_mod & {
			background-color: $coral;
			color: $black_2;
			border-color: $black_2;
		}
	}
}

@mixin btn_style_white_2 {
	font-weight: 700;
	letter-spacing: 0.03em;

	transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease;
	border: 2px solid $black_2;

	.news_banner--bg_black_mod & {
		border-color: $white_2;
		color: $white_2;
	}

	@include st_btn;

	&:hover {
		background-color: $black_2;
		color: $white_2;
	}

	@include bp($point_2, min) {
		background-color: rgba($black_2, 0);
		color: $black_2;
	}

	@include bp($point_3) {
		color: $white_2;
		background-color: $black_2;
	}
}

@mixin btn_style_white_3 {
	font-weight: 700;
	letter-spacing: 0.03em;

	transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease;
	border: 2px solid $black_2;

	.news_banner--bg_black_mod & {
		border-color: $white_2;
		color: $white_2;
	}

	@include st_btn;

	&:hover {
		background-color: $black_2;
		color: $white_2;
	}

	@include bp($point_2, min) {
		background-color: rgba($black_2, 0);
		color: $black_2;
	}
	@include bp($point_3) {
		color: $white_2;
		background-color: $black_2;
		border: 1px solid $white_2;
	}
}

@mixin btn_style_coral {
	font-weight: 700;
	transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease;
	@include st_btn;

	@include bp($point_2, min) {
		color: $white_2;
		background-color: $black_2;
		border: 1px solid $white_2;

		&:hover {
			background-color: $coral;
			color: $black_2;
			border-color: $black_2;
		}
	}

	@include bp($point_3) {
		background-color: $coral;
		color: $black_2;
		border-color: $black_2;
		&:hover {
			color: $white_2;
			background-color: $black_2;
			border: 1px solid $white_2;
		}
	}
}

//size mixins:
@mixin btn_size_normal {
	height: 5.6rem;
	padding: 0 2.8rem;

	line-height: 5.6rem;
	font-size: 1.6rem;

	border-radius: $gl_radius;
}
